import React, { useEffect, useState } from 'react';
import logo from '../logo.svg';
import '../App.css';
import { AbsoluteCenter, Box, Button, Modal, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalHeader, ModalOverlay, SimpleGrid, Spinner, useDisclosure } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import DefaultUtils from '../lib/DefaultUtils';
import { Base64 } from 'js-base64';

export default function LoginPage() {
  const queryParameters = new URLSearchParams(window.location.search)
  const msg = queryParameters.get("msg")
  const navigate = useNavigate();
  const { isOpen, onOpen, onClose } = useDisclosure();

  const [errMsg, setErrMsg] = useState<string>();

  const [isLoading, setLoading] = useState<boolean>(false);
  useEffect(() => {

    const temp = async () => {

      let data = DefaultUtils.decodeBase64(queryParameters);
      let errMsg = data?.errMsg as string;

      if (errMsg) {
        onOpen();
      }
      setErrMsg(errMsg);
    };
    temp();
  }, []);
    return (
      <>
        <SimpleGrid
        columns={1}
        spacing='8'
        p='10'
        textAlign='center'
        rounded='lg'
        color='gray.400'
      >
        <Box position='relative' h='100vh'>
          <Modal
            isCentered
            onClose={onClose}
            isOpen={isOpen}
            motionPreset='slideInBottom'
          >
            <ModalOverlay />
            <ModalContent>
              <ModalHeader>Modal Title</ModalHeader>
              <ModalCloseButton />
              <ModalBody>
                {errMsg}
              </ModalBody>
              <ModalFooter>
                <Button colorScheme='blue' mr={3} onClick={onClose}>
                  Close
                </Button>
              </ModalFooter>
            </ModalContent>
          </Modal>
          <AbsoluteCenter p='100'  axis='both'>
            {isLoading && <Spinner size='xl' />}
            <Button colorScheme='teal' size='lg' onClick={e => {
              setLoading(true);
              (window as any).toApp.postMessage(JSON.stringify({
                "type" : 'google-signin'
              }));
            }}>
              Sign In for Google
            </Button>

          </AbsoluteCenter>
        </Box>
      </SimpleGrid>
      </>
    )
}