import React, { useEffect, useState } from 'react';
import LoadingPage from './LoadingPage';
import { Box, Button, Card, CardBody, CardHeader, Center, Flex, Heading, SimpleGrid, Spacer, Square, Stack, Stat, StatArrow, StatGroup, StatHelpText, StatLabel, StatNumber, Text } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import DefaultUtils from '../lib/DefaultUtils';
import moment from 'moment';

export default function ViewPage() {
  const queryParameters = new URLSearchParams(window.location.search)
  const msg = queryParameters.get("msg")
  const navigate = useNavigate();

  const [dateToData, setDateToData] = useState<Record<string,any>>();
  const [data, setData] = useState<any[]>();
  const [todayData, setTodayData] = useState<any[]>();

  const [isLoading, setLoading] = useState<boolean>(true);
  const [keyToAvg, setKeyToAvg] = useState<Record<string,string>>();

  const [param, setParam] = useState<any>();

  const adsense_host = "https://adsense.googleapis.com";

  //https://developers.google.com/adsense/management/reference/rest/v2/Metric
  const metrics = [
    "PAGE_VIEWS",
    "AD_REQUESTS",
    "MATCHED_AD_REQUESTS",
    "TOTAL_IMPRESSIONS",
    "IMPRESSIONS",
    "INDIVIDUAL_AD_IMPRESSIONS",
    "CLICKS",
    "AD_REQUESTS_COVERAGE",
    "PAGE_VIEWS_CTR",
    "AD_REQUESTS_CTR",
    "MATCHED_AD_REQUESTS_CTR",
    "IMPRESSIONS_CTR",
    "INDIVIDUAL_AD_IMPRESSIONS_CTR",
    "ACTIVE_VIEW_MEASURABILITY",
    "ACTIVE_VIEW_VIEWABILITY",
    "ACTIVE_VIEW_TIME",
    "ESTIMATED_EARNINGS",
    "PAGE_VIEWS_RPM",
    "AD_REQUESTS_RPM",
    "MATCHED_AD_REQUESTS_RPM",
    "IMPRESSIONS_RPM",
    "INDIVIDUAL_AD_IMPRESSIONS_RPM",
    "COST_PER_CLICK",
    "ADS_PER_IMPRESSION",
    "TOTAL_EARNINGS",
    "WEBSEARCH_RESULT_PAGES",
    "FUNNEL_REQUESTS",
    "FUNNEL_IMPRESSIONS",
    "FUNNEL_CLICKS",
    "FUNNEL_RPM"
  ];

  let arr : string[] = [];


  const fetchData = async (ids : string[], header : Record<string, string>, metric : string[], today : boolean = false) => {
    let _dateToData :Record<string,any> = {};
    let nameToAvg : Record<string,string> = {};

    for (let row of ids) {
      let metricParam = "";
      for (let row2 of metric) {
        metricParam += `&metrics=${row2}`;
      }

      let url = '';
      if (today) {
        url = `${adsense_host}/v2/` + row + `/reports:generate?dateRange=TODAY&dimensions=DATE&${metricParam}&alt=json`;
      }
      else {
        url = `${adsense_host}/v2/` + row + `/reports:generate?dateRange=LAST_7_DAYS&dimensions=DATE&${metricParam}&alt=json`;
      }

      let res = await fetch(url, {
        method: 'GET',
        headers : header,
      });
      
      let json = await res.json();

      if (json?.error) {
        // if (json.error.status === "'UNAUTHENTICATED") {

        // }
        navigate("/login");
        return
      }

      let nameToIdx : Record<string,number> = {};
      let idxToName : Record<number,string> = {};
      let lastIdx = -1;
      for (let i=0; i<json.headers.length; i++) {
        let row = json.headers[i];
        if (row.name === "DATE") {
          nameToIdx["DATE"] = i;
        }
        else {
          nameToIdx[row.name] = i;
          idxToName[i] = row.name;
        }

        if (json.headers.length -1 == i) {
          lastIdx = i;
        }
      }
      for (let i=0; i<json.averages.cells.length; i++) {
        let row = json.averages.cells[i];

        if (Object.keys(row).length <= 0) {
          continue;
        }

        let name = idxToName[i];
        if (row.name === "DATE") {
          continue;
        }
        nameToAvg[name] = row.value;
      }

      for (let row of json.rows) {
        let date = "";
        for (let i=0; i<row.cells.length; i++) {
          let row2 = row.cells[i];
          if (nameToIdx["DATE"] === i) {
            date = row2.value;
          }
          else {
            let data : Record<string,any> = {};
            if (DefaultUtils.keyExists(_dateToData, date)) {
              data = _dateToData[date]
            }
            let metric = idxToName[i];
            let temp = 0;
            if (DefaultUtils.keyExists(data, `${metric}`)) {
              temp = Number(data[`${metric}`])
            }
            temp += Number(row2.value);
            data[`${metric}`] = ""+temp;
            _dateToData[date] = data;
          }
        }
      }
    }
    let arr = [];
    for (let dateKey of Object.keys(_dateToData)) {
      let temp_obj : Record<string,string> = {'DATE' : dateKey};
      for (let row2 of Object.keys(_dateToData[dateKey])) {
        temp_obj[row2] = _dateToData[dateKey][row2];
      }
      arr.push(temp_obj);
    }

    if (today) {
      setTodayData(arr.reverse());
      setLoading(false);
    }
    else {
      console.log(`_dateToData ${JSON.stringify(_dateToData)}`)
      setData(arr.reverse());
      setKeyToAvg(nameToAvg);
      setDateToData(_dateToData);
    }
  }

  useEffect(() => {

    const temp = async () => {
      let data = DefaultUtils.decodeBase64(queryParameters);
      setParam(data);
      let ids = data.ids as string[];
      let header = data.auth_header as Record<string, string>;
      await fetchData(ids, header, ["PAGE_VIEWS", "AD_REQUESTS", "CLICKS", "TOTAL_IMPRESSIONS", "PAGE_VIEWS_RPM", "ESTIMATED_EARNINGS"]);
      await fetchData(ids, header, ["PAGE_VIEWS", "AD_REQUESTS", "CLICKS", "TOTAL_IMPRESSIONS", "PAGE_VIEWS_RPM", "ESTIMATED_EARNINGS"], true);

      console.log(`dateToData ${JSON.stringify(dateToData)}`);
    };
    temp();
  }, []);

    return (
      <>
      {isLoading ? <LoadingPage param={param} /> :
        <>
          <SimpleGrid
            columns={1}
            spacing='8'
            p='10'
            textAlign='center'
            rounded='lg'
            mt={10}
          >
            <StatGroup>
              <Stat>
                <StatLabel>Earn</StatLabel>
                <StatNumber>{todayData ? DefaultUtils.currency(Number(todayData[0].ESTIMATED_EARNINGS)) : ""}</StatNumber>
                <StatHelpText>
                  <StatArrow type={todayData && keyToAvg && DefaultUtils.keyExists(keyToAvg, 'ESTIMATED_EARNINGS') && DefaultUtils.avgPer(Number(todayData[0].ESTIMATED_EARNINGS), Number(keyToAvg['ESTIMATED_EARNINGS'])) > 0 ? 'increase' : 'decrease' } />
                  {todayData && keyToAvg && DefaultUtils.keyExists(keyToAvg, 'ESTIMATED_EARNINGS') ? 
                  <>
                  {DefaultUtils.avgPer(Number(todayData[0].ESTIMATED_EARNINGS), Number(keyToAvg['ESTIMATED_EARNINGS'])) +"%"}
                  </>  : ""}
                </StatHelpText>
              </Stat>

              <Stat>
                <StatLabel>Page View</StatLabel>
                <StatNumber>{todayData ? Number(todayData[0].PAGE_VIEWS).toLocaleString() : ""}</StatNumber>
                <StatHelpText>
                  <StatArrow type={todayData && keyToAvg && DefaultUtils.keyExists(keyToAvg, 'PAGE_VIEWS') && DefaultUtils.avgPer(Number(todayData[0].PAGE_VIEWS), Number(keyToAvg['PAGE_VIEWS'])) > 0 ? 'increase' : 'decrease' } />
                  {todayData && keyToAvg && DefaultUtils.keyExists(keyToAvg, 'PAGE_VIEWS') ? DefaultUtils.avgPer(Number(todayData[0].PAGE_VIEWS), Number(keyToAvg['PAGE_VIEWS'])) +"%"  : ""}
                </StatHelpText>
              </Stat>
            </StatGroup>
          </SimpleGrid>
          <Stack spacing='4'>
            {data ? data?.map((row) => {
              return (
                <Card key={`${row.DATE}-sm`} size={"sm"}>
                  <CardHeader>
                    <Heading size='md'>
                      <Stat>
                        {/* <StatLabel>Collected Fees</StatLabel> */}
                        <StatNumber>
                          {DefaultUtils.currency(Number(row.ESTIMATED_EARNINGS))}
                          <StatArrow type={keyToAvg && DefaultUtils.keyExists(keyToAvg, 'ESTIMATED_EARNINGS') && DefaultUtils.avgPer(Number(row.ESTIMATED_EARNINGS), Number(keyToAvg['ESTIMATED_EARNINGS'])) > 0 ? 'increase' : 'decrease' } />
                            {keyToAvg && DefaultUtils.keyExists(keyToAvg, 'ESTIMATED_EARNINGS') ? DefaultUtils.avgPer(Number(row.ESTIMATED_EARNINGS), Number(keyToAvg['ESTIMATED_EARNINGS'])) +"%"  : ""}
                        </StatNumber>
                        <StatHelpText>
                          {moment(row.DATE, "YYYY-MM-DD").fromNow()}
                        </StatHelpText>
                      </Stat>
                    </Heading>
                  </CardHeader>
                  <CardBody>
<Flex>
  <Box flex="1">
    <Text>REQUESTS : {Number(row.AD_REQUESTS).toLocaleString()}</Text>
    <Text>PAGE VIEWS : {Number(row.PAGE_VIEWS).toLocaleString()}</Text>
  </Box>
  <Box textAlign={'right'} flex='1'>
    <Text>IMPRESSIONS : {Number(row.TOTAL_IMPRESSIONS).toLocaleString()}</Text>
    <Text>CLICKS : {Number(row.CLICKS).toLocaleString()}</Text>
  </Box>
</Flex>

                    
                  </CardBody>
                  
                </Card>
              )
            }) : ""}

          </Stack>
        </>
      }
            <Button colorScheme='teal' size='lg' onClick={e => {
              (window as any).toApp.postMessage(JSON.stringify({
                type : 'google-signout'
              }));
              setLoading(true);
            }}>
              Logout
            </Button>
      </>
      
    )
}