import React, { useEffect, useState } from 'react';
import { AbsoluteCenter, Box, Progress, SimpleGrid, Spinner } from '@chakra-ui/react';
import DefaultUtils from '../lib/DefaultUtils';

export interface LoadingPage {
  width? : string
  height? : string
  param? : any
}
export default function LoadingPage(args : LoadingPage) {
  const [height, setHeight] = useState<string>(args.width || '');
  const [width, setWidth] = useState<string>(args.height || '100vh');


  useEffect(() => {
    if (args?.param) {
      args?.param.width && setWidth(args?.param.width);
      args?.param.height && setHeight(args?.param.height);
    }
  }, []);

    return (
      <SimpleGrid
        columns={1}
        spacing='8'
        p='10'
        textAlign='center'
        rounded='lg'
        color='gray.400'
      >
        <Box position='relative' h={height || '100vh'}>
          
          <AbsoluteCenter p='100'  axis='both'>
            <Spinner size='xl'
              emptyColor='gray.200'
              color='green.500'
            />

          </AbsoluteCenter>
        </Box>
      </SimpleGrid>
    )
}